import React from 'react';
import { Card, CardContent, Typography, LinearProgress, Grid, CircularProgress } from '@material-ui/core';

import { msToString, betterDateString } from './utils';
import user from '../services/user';
import db from '../services/db';
const firestore = db.firestore();
class Sessions extends React.Component {

  constructor() {
    super();
    this.state = {
      totalTime: 0,
      sessions: null,
      loading: true,
      streamSub: null,
    }
  }
  componentDidMount() {
    firestore.collection("users").doc(user).onSnapshot((snapshot) => {
      const sessions = [];
      const data = snapshot.data();
      var totalTime = 0;
      for (let [key, value] of Object.entries(data)) {
        totalTime += value;
        sessions.push({
          date: key,
          length: value,
        });
      }
      sessions.sort((a, b) => {
        if (a.date < b.date)
          return 1;
        if (a.date > b.date)
          return -1;
        return 0;
      });
      this.setState({
        sessions: sessions,
        loading: false,
        totalTime: totalTime,
      });
    });
  }

  render() {
    if (this.state.loading) {
      return (
        <Grid container justify="center">
        </Grid>
      );
    }
    return (
      <React.Fragment>
        <Grid container justify="center" direction="row" spacing={2} alignItems="center" >
          <Grid item xs={12}>
            <Card style={{ margin: "1%" }}>
              <CardContent style={{ textAlign: 'center' }}>
                <Typography variant="h5" component="h2">
                  {msToString(this.state.totalTime)}
                </Typography>
                <Typography color="textSecondary" >
                  spent on Competitive Programming
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          {this.state.loading ?
            <Grid item> <CircularProgress /> </Grid> :
            this.state.sessions.map((e, id) => <Grid item key={id}>
              <Card>
                <LinearProgress value={Math.min(Math.floor(e.length / 10320000 * 100), 100)} variant="determinate" />
                <CardContent style={{ textAlign: 'center' }}>
                  <Typography variant="h5" component="h2">
                    {msToString(e.length)}
                  </Typography>
                  <Typography color="textSecondary">
                    {betterDateString(e.date)}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            )}
        </Grid>
      </React.Fragment>
    );
  }
}

export default Sessions;