import React, { Component } from 'react';
import { Card, CardContent, Typography, Grid, CircularProgress } from '@material-ui/core';
import { msToString } from './utils';

import db from '../services/db';
const firestore = db.firestore();

class All extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      users: []
    }
  }

  componentDidMount() {
    firestore.collection("users").onSnapshot(snapshot => {
      var users = snapshot.docs.map((e) => {
        const data = e.data();
        var totalTime = 0;
        for (let [_, value] of Object.entries(data)) {
          totalTime += value;
        }
        return {
          user: e.id,
          totalTime
        };
      });
      this.setState({
        loading: false,
        users
      })
    });
  }

  render() {
    if (this.state.loading) {
      return (
        <React.Fragment>
        <Grid container justify="center">
        </Grid>
        </React.Fragment>
      );
    }
    return (
      <React.Fragment>
        <Grid container justify="center" direction="row" spacing={2} alignItems="center" >
          {this.state.loading ?
            <Grid item> <CircularProgress /> </Grid> :
            this.state.users.map((e, id) => <Grid item key={id}>
              <Card style={{ margin: "1%" }}>
                <CardContent style={{ textAlign: 'center' }}>
                  <Typography variant="h5" component="h2">
                    {msToString(e.totalTime)}
                  </Typography>
                  <Typography color="textSecondary" >
                    {e.user}
                </Typography>
                </CardContent>
              </Card>
            </Grid>
            )}
        </Grid>
      </React.Fragment>
    );
  }
}

export default All;