import React from 'react';

import { Card, CardContent, Typography, Button, CircularProgress, Grid, LinearProgress } from '@material-ui/core';
import { msToString, today } from './utils';
import user from '../services/user';
import db from '../services/db';

const firestore = db.firestore();

class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      startTime: null,
      running: false,
      dbIntervalId: null,
      localIntervalId: null,
      localTime: 0,
      loading: null,
      untilNow: null,
    }
  }
  fetchData = async () => {
    this.setState({ loading: true });
    const data = (await firestore.collection("users").doc(user).get()).data();
    var untilNow;
    untilNow = data[today()];
    untilNow = untilNow === undefined ? 0 : untilNow;
    this.setState({
      localTime: untilNow,
      untilNow: untilNow,
      loading: false,
    })
  }

  componentDidMount() {
    this.fetchData();
  }

  dbAction = () => {
    const key = today();
    const value = Date.now() - this.state.startTime;//milliseconds
    firestore.collection("users").doc(user).update({ [key]: this.state.untilNow + value });
  }

  updateTimer = () => {
    this.setState({
      localTime: this.state.localTime + 1000
    });
  }

  toggleTimer = () => {
    var stateSnap = { ...this.state };
    if (this.state.running === false) {
      stateSnap.dbIntervalId = setInterval(this.dbAction, 60000);
      stateSnap.localIntervalId = setInterval(this.updateTimer, 1000);
      stateSnap.startTime = Date.now();
    } else {
      clearInterval(stateSnap.dbIntervalId);
      clearInterval(stateSnap.localIntervalId);
      console.log(`Error rate : ${(Date.now() - this.state.startTime) - (this.state.localTime - this.state.untilNow)} ms`);
    }
    stateSnap.running = !stateSnap.running;
    this.setState(stateSnap, () => {
      if (!stateSnap.running) {
        this.fetchData();
      }
    });
  }

  render() {
    if (this.state.loading && this.state.untilNow == null) {
      return (
        <div
          style={{
            position: 'absolute', left: '50%', top: '50%',
            transform: 'translate(-50%, -50%)'
          }}
        >
          <CircularProgress />
        </div>
      );
    }
    return (
      <React.Fragment>
        <Grid container justify="center" direction="column" alignItems="center" spacing={4}>
          <Grid item >
            <Card>
              <LinearProgress value={0} variant={this.state.running ? "indeterminate" : "determinate"} />

              <CardContent style={{ textAlign: 'center' }}>
                <Typography variant="h5" component="h2">
                  {user.toString()}
                </Typography>
                <Typography component="p">{msToString(this.state.localTime)}</Typography>
                <Button color={this.state.running ? "secondary" : "primary"} variant="contained" onClick={this.toggleTimer}>{this.state.running ? "Stop" : "Start"}</Button>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }
}

export default Timer;